.myShopHeader {
    padding-left: 1.989em;
    padding-bottom: 0.737em;
    vertical-align: bottom;
    margin-top: 0.737em; /* 10px */
    margin-bottom: 2.21em;
}
.myShopHeader #myShop {
    font-size: 2.6em;
    margin-right: 0.7086em; /* 25px */
}
.myShopHeader .locationButton {
    cursor: pointer;
    font-family: SpoqaRegular;
}
.myShopHeader .locationButton #location{
    position: relative;
    font-size: 1.2em;
    color: rgba(0,0,0,0.6);
    margin-left: 0.246em; /* 4px */
    border-bottom: 1px solid #707070;
    bottom: 0.492em;
    user-select: none;
}

.shopCategory {
    width: inherit;
    background-color: white;

    display: flex;
    align-items: center;
}

.shopCategory > div {
    text-align: center;
    float: left;
    width: 33.333%;
    padding-top: 0.526em; /* 10px */
    padding-bottom: 0.526em;

    font-size: 1.4em;
    font-family: SpoqaRegular;
    color: rgba(0,0,0,0.36);
    cursor: pointer;
    user-select: none;
}

.shopCategory .active {
    padding-bottom: 0.3682em;
    border-bottom: 0.1578em solid #3FB8D5;
    color: rgba(0,0,0,0.65);
}

.menuChange {
    padding-top: 2.211em;
    padding-right: 5%;
    background-color: rgba(0,0,0,0.03);
    display: flex;
    display: -webkit-flex;
    justify-content: flex-end;
    -webkit-box-pack: flex-end;
    color: #3FB8D5;
}

.menuChange .menuChangeButton {
    border: 1px solid #3FB8D5;
    border-radius: 1.106em;
    padding-top: 0.134em;
    padding-bottom: 0.134em;
    padding-left: 1.34em;
    padding-right: 0.67em; /* 10px */
    cursor: pointer;
    font-size: 1.1em;
    user-select: none;
}

.menuChange .menuChangeButton > span{
    margin-left: 0.67em;
}

.newPage {
    width: 100%;
    border: none;
    height: 50vh;
}

.fadeOverlay-enter {
    opacity: 0.01;
}
  
.fadeOverlay-enter-active {
    opacity: 1;
    transition: all 200ms ease-in;
}
  
.fadeOverlay-exit {
    opacity: 1;
}
  
.fadeOverlay-exit-active {
    opacity: 0.01;
    transition: all 200ms ease-in;
}